import React, { useState } from 'react'


import TweetList from "../tweets/components/TweetList"
import '../styles/Home.css'
import TweetForm from "../tweets/components/TweetForm"
import Sidebar from "../tweets/components/Sidebar"

function Home(props) {
    const [newTweets, setNewTweets] = useState([])

    return (
        <div className="home-container">
            <Sidebar />
            <div className="twitter_feed">
                <TweetForm  newTweets={newTweets} setNewTweets={setNewTweets} props={props} />
                <TweetList newTweets={newTweets} setNewTweets={setNewTweets} props={props} />
            </div>
            <Sidebar />     
        </div>
    )
}

export default Home
