import { lookup } from '../../tweet_lookup/lookup'

export function apiTweetCreate(newTweet, callBack, user) {
    lookup("POST", "/tweets/create/", callBack, {content: newTweet, user: user})
}
  
export function apiTweetList(username, callback) {
    let endpoint = '/tweets/'

    if (username) {
        endpoint += `?username=${username}`
    }
    lookup("GET", endpoint, callback)
}

export function apiTweetDetail(tweetId, callback) {
    let endpoint = '/tweets/'

    if (tweetId) {
        endpoint += `${tweetId}`
    }
    lookup("GET", endpoint, callback)
}

export function apiTweetAction(tweetId, action, callback, user) {
    const data = {id: tweetId, action: action, user: user}
    lookup("POST", "/tweets/action/", callback, data)
}

export function apiGetUser(username, callback) {
    lookup("GET", "/get_user", callback, {username: username})
}
