import { useEffect, useState } from 'react'
import Tweet from './Tweet'
import { apiTweetList } from '../tweet_functions'
import '../styles/TweetList.css'


const TweetList = ({ newTweets, setNewTweets, props }) => {
    
    const [tweets, setTweets] = useState([])
    const [tweetsDidSet, setTweetsDidSet] = useState(false)

    function myCallback (response, status) {
      if (status === 200) {
        setTweets(response)
        setTweetsDidSet(true)
      } else {
        alert("There was an error")
      }
    };

    useEffect(() => {
      if (newTweets.length > 0) {
        setTweets([...newTweets, ...tweets])
        setNewTweets([])
      }
      // eslint-disable-next-line
    }, [newTweets]);
    
    useEffect(() => {
      if (tweetsDidSet === false) {
        apiTweetList(props.username, myCallback)
        setTweetsDidSet(true)
      }

    }, [tweets, tweetsDidSet, setTweetsDidSet, props.username]);

    return (
        <div className="tweet_list__container">
            {tweets.map((tweet, idx) => {return <Tweet tweet={tweet} idx={idx} key={`${idx}-{tweet.id}`} callback={myCallback} props={props}/>})}
        </div>
    )
}

export default TweetList